      border-radius: 4px;\n                    display: flex;\n\n                    &:nth-child(3n) {\n                         margin-right: 0;\n                    }\n\n                    label {\n                         display: flex;\n                         flex-direction: column;\n                         width: 100%;\n                         justify-content: center;\n                         align-items: center;\n                         text-align: center;\n                         line-height: 1.2;\n                         font-size: 10px;\n                         text-transform: uppercase;\n                         font-weight: 700;\n                         border-radius: 4px;\n\n                         &.selected {\n                              input {\n                                   outline: 2px solid #2271b1;\n                                   outline-offset: 3px;\n                              }\n                         }\n\n                         input[type=\"radio\"] {\n                              width: 3rem;\n                              height: 36px;\n                              border-radius: 4px;\n                              margin: 0;\n                              margin-bottom: 0.5rem;\n\n                              &[value=\"red\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"dark-red\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"light-red\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"off-white\"] {\n                                   background-color: $pf-off-white;\n                              }\n\n                              &[value=\"white\"] {\n                                   background-color: $pf-white;\n                              }\n\n                              &[value=\"charcoal\"] {\n                                   background-color: $pf-charcoal;\n                              }\n\n                              &[value=\"raspberry\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"yellow\"] {\n                                   background-color: $pf-yellow;\n                              }\n\n                              // &[value=\"light-green\"] {\n                              //      background-color: $pf-light-green;\n                              // }\n\n                              &[value=\"dark-green\"] {\n                                   background-color: $pf-dark-green;\n                              }\n\n                              &:checked {\n                                   &::before {\n                                        display: none;\n                                   }\n                              }\n\n                              &:focus {\n                                   box-shadow: none;\n                                   border-color: none;\n                              }\n                         }\n                    }\n               }\n          }\n     }\n}","$pf-base-size: 1rem;\n\n/**********************\n Container sizes\n***********************/\n$col--sm: 540px; // (max-width: 540px)\n$col--md: 720px; // (max-width: 720px)\n$col--lg: 960px; // (max-width: 960px)\n$col--xl: 1140px; // (max-width: 1140px)\n\n/**********************\n Colours\n***********************/\n\n// Brand colours\n$pf-off-white: #f8faf8;\n$pf-charcoal: #262626;\n$pf-white: #fff;\n$pf-raspberry: #993050;\n$pf-secondary-raspberry: #77203A;\n$pf-yellow: #FAB200;\n$pf-yellow--alt: #E0A000;\n$pf-shadow: #464B44;\n$pf-light-green: #60CE71;\n$pf-dark-green: #2D6E51;\n$pf-grey: #D9D9D9;\n\n\n\n// Text colours\n$body-text-color: $pf-charcoal;\n\n/**********************\n Breakpoints\n***********************/\n\n$breakpoints: (\n  sm: (breakpoint: 576px,\n    max-width: 540px,\n  ),\n  md: (breakpoint: 768px,\n    max-width: 720px,\n  ),\n  lg: (breakpoint: 992px,\n    max-width: 960px,\n  ),\n  xl: (breakpoint: 1200px,\n    max-width: 1140px,\n  ),\n  xxl: (breakpoint: 1650px,\n  ),\n);\n\n$size--xs: $pf-base-size * 0.6875;\n//11px\n$size--sm: $pf-base-size * 0.8125;\n//13px\n$size--base: $pf-base-size;\n//16px\n$size--md: $pf-base-size * 1.125;\n//18px\n$size--lg: $pf-base-size * 1.5;\n//24px\n$size--xl: $pf-base-size * 1.875;\n// 30px\n$size--xxl: $pf-base-size * 3.125;\n//50px\n$size--giant: $pf-base-size * 4.6875;\n\n\n/**********************\n Spacing\n***********************/\n\n$spacing--xs: $pf-base-size * 1;\n$spacing--sm: $pf-base-size * 1.5;\n$spacing--md: $pf-base-size * 2;\n$spacing--lg: $pf-base-size * 3;\n$spacing--xl: $pf-base-size * 4;\n$spacing--xxl: $pf-base-size * 5;\n$spacing--header: $pf-base-size * 6;\n$spacing--header-lg: $pf-base-size * 8;\n\n/**********************\nFonts\n***********************/\n\n$font--primary: 'Jost', 'Avenir', 'Futura', 'Century Gothic', Helvetica, Arial, sans-serif;\n$font--secondary: 'Katibeh', 'Unna', 'Big Caslon', Georgia, serif;\n$font--tertiary: 'Montserrat', Helvetica, Arial, sans-serif;\n$font--fun: 'Knewave', Helvetica, Arial, sans-serif;\n\n\n/**********************\nHeader\n***********************/\n$header-height-mobile: 60px;\n$header-height-desktop: 70px;","// Modern CSS reset: https://github.com/hankchizljaw/modern-css-reset\n\n/* Box sizing rules */\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n// /* Remove default padding */\n// ul[class],\n// ol[class] {\n//     padding: 0;\n// }\n\n/* Remove default margin */\nbody,\nh1,\nh2,\nh3,\nh4,\np,\nul,\nol,\nfigure,\nblockquote,\ndl,\ndd {\n    margin: 0;\n}\n\n/* Set core root defaults */\nhtml {\n    scroll-behavior: smooth;\n}\n\n/* Set core body defaults */\nbody {\n    min-height: 100vh;\n    text-rendering: optimizeSpeed;\n    line-height: 1.5;\n}\n\n/* Remove list styles on ul, ol elements with a class attribute */\nul[class],\nol[class] {\n    list-style: none;\n}\n\n/* A elements that don't have a class get default styles */\na:not([class]) {\n    text-decoration-skip-ink: auto;\n}\n\n/* Make images easier to work with */\nimg,\npicture {\n    max-width: 100%;\n    display: block;\n}\n\n/* Natural flow and rhythm in articles by default */\narticle>*+* {\n    margin-top: 1em;\n}\n\n/* Inherit fonts for inputs and buttons */\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\n/* Blur images when they have no alt attribute */\nimg:not([alt]) {\n    filter: blur(10px);\n}\n\n/* Remove all animations and transitions for people that prefer not to see them */\n@media (prefers-reduced-motion: reduce) {\n    * {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}","/**\nThe function `map-deep-get` is a custom function written in a Sass/SCSS stylesheet. Its purpose is to retrieve a nested value from a map (also known as a dictionary or associative array) using a variable number of keys.\n\nHere's a breakdown of how the function works:\n\n1. The function is defined using the `@function` directive and named `map-deep-get`. It takes two parameters:\n   - `$map`: The map from which the value will be retrieved.\n   - `$keys...`: A variable number of keys that specify the path to the desired value in the map.\n\n2. Inside the function, there is a `@each` loop that iterates over each `$key` provided in `$keys...`.\n\n3. Within the loop, the `map-get()` function is used to retrieve the value associated with the current `$key` from the `$map`. The retrieved value is then assigned back to the `$map` variable, effectively updating it to the nested map.\n\n4. After the loop finishes, the final value of `$map` is returned using the `@return` directive.\n\nIn summary, the `map-deep-get` function allows you to access a nested value within a map by providing a sequence of keys that define the path to the desired value. Each key is used to access a nested map until the final value is reached, which is then returned.\n*/\n\n@function map-deep-get($map, $keys...) {\n\t@each $key in $keys {\n\t\t$map: map-get($map, $key);\n\t}\n\t@return $map;\n}","/**\n * Background Colours\n * -----------------------------------------------------------------------------\n */\n\n.bg--red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--dark-red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--light-red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--off-white {\n    background-color: $pf-off-white;\n    color: $pf-charcoal;\n}\n\n.bg--white {\n    background-color: $pf-white;\n    color: $pf-charcoal;\n}\n\n.bg--charcoal {\n    background-color: $pf-charcoal;\n    color: $pf-white;\n}\n\n.bg--yellow {\n    background-color: $pf-yellow;\n    color: $pf-charcoal;\n}\n\n.bg--yellow-alt {\n    background: $pf-yellow--alt;\n    color: $pf-charcoal;\n}\n\n.bg--raspberry {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--dark-green {\n    background-color: $pf-dark-green;\n    color: $pf-white;\n}\n\n/**\n * Text Colours\n * -----------------------------------------------------------------------------\n */\n.red,\n.raspberry {\n    color: $pf-raspberry;\n}\n\n.dark-red,\n.dark-raspberry {\n    color: $pf-raspberry;\n}\n\n.light-red {\n    color: $pf-raspberry;\n}\n\n.off-white {\n    color: $pf-off-white;\n}\n\n.white {\n    color: $pf-white;\n}\n\n.charcoal {\n    color: $pf-charcoal;\n}\n\n.dark-green {\n    color: $pf-dark-green;\n}\n\n.yellow {\n    color: $pf-yellow;\n}","// Primary font - Jost (weights 0 - 800)\n@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\n\n// Secondary font - Katibeh\n@import url('https://fonts.googleapis.com/css2?family=Katibeh&display=swap');\n\n// Tertiary font - Knewave\n@import url('https://fonts.googleapis.com/css2?family=Knewave&display=swap');\n\np,\nli,\na {\n  font-family: $font--primary;\n  font-size: $size--md;\n  font-weight: 300;\n  line-height: $spacing--md;\n}\n\n.text__standfirst {\n  li, p, a {\n    font-family: $font--primary;\n    font-size: $size--lg;\n    font-weight: 400;\n    line-height: $spacing--md;\n    padding-bottom: 1.5rem;\n  }\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: $font--primary;\n  text-transform: uppercase;\n  font-weight: 700;\n}\n\n.h1,\n.h2,\n.h3,\n.h4,\n.h5,\n.h6 {\n  font-family: $font--primary;\n}\n\nh2 {\n  letter-spacing: 2px;\n}\n\n.giant-h1 {\n  font-family: $font--primary;\n  font-weight: 600;\n  font-size: $size--xxl;\n  line-height: 1.2;\n\n  @include media-breakpoint('lg') {\n    font-size: $size--giant;\n    line-height: 1.2;\n  }\n}\n\n.h1,\nh1:not([class]),\n.heading__h1 {\n  font-size: $size--xxl;\n}\n\n.h2,\nh2:not([class]),\n.heading__h2 {\n  font-size: $size--xl;\n}\n\n.h3,\nh3:not([class]),\n.heading__h3 {\n  font-size: $size--lg;\n}\n\n.h4,\nh4:not([class]),\n.heading__h4 {\n  font-size: $size--md;\n}\n\n.h5,\nh5:not([class]),\n.heading__h5 {\n  font-size: $size--base;\n}\n\n.h6,\nh6:not([class]),\n.heading__h6 {\n  font-size: $size--sm;\n}\n\na {\n  color: $pf-raspberry;\n  text-decoration: underline;\n}\n\nul:not(.acf-radio-list):not(.acf-tab-group) {\n  li:not([class]) {\n    list-style: none;\n  }\n\n  li:not([class]):not(.editor-styles-wrapper):before {\n    content: \"·\";\n    font-size: 30px;\n    vertical-align: middle;\n    line-height: 20px;\n    padding-right: 0.5rem;\n  }\n}\n\nol {\n  margin-left: 1rem;\n\n  li {\n    list-style-type: decimal;\n\n    li {\n      margin-left: 1rem;\n    }\n  }\n}\n\n.button {\n  font-size: rem(18);\n  letter-spacing: 2px;\n\n  @include media-breakpoint('lg') {\n    font-size: rem(22);\n  }\n}\n\n.nav {\n  ul {\n    li {\n      a {\n        font-size: $size--base;\n        text-decoration: none;\n      }\n    }\n  }\n}\n\n.quote {\n  font-size: $size--lg;\n  line-height: $spacing--md;\n  margin: auto;\n  margin-top: 4rem;\n  line-height: 3rem;\n\n  @include media-breakpoint('sm') {\n    width: 80%;\n  }\n\n  @include media-breakpoint('md') {\n    width: 60%;\n  }\n\n\n\n  &__mark {\n    display: inline-block;\n    width: 3rem;\n    height: 1rem;\n    position: relative;\n    \n    span {\n      color: $pf-raspberry;\n      font-size: 100px;\n      position: absolute;\n    }\n\n    &--before {\n      left: -5px;\n    }\n  }\n  \n  &__short {\n    font-weight: 600;\n    text-transform: uppercase;\n  }\n\n  &__long {\n    font-weight: 400;\n  }\n\n  &__author {\n    font-size: $size--lg;\n    font-weight: 400;\n    line-height: $spacing--md;\n    margin: auto;\n    margin-top: 2rem;\n    color: $pf-raspberry;\n    text-align: right;\n\n    @include media-breakpoint('sm') {\n      width: 80%;\n    }\n\n    @include media-breakpoint('md') {\n      width: 60%;\n    }\n  }\n}\n\n\n\n@include media-breakpoint('lg') {\n  .nav {\n    font-size: $size--md;\n\n    .menu-item:hover {\n      a {\n        color: $pf-white;\n      }\n    }\n\n    .sub-menu {\n      font-size: $size--md;\n    }\n  }\n}\n",".footer {\n    color: $pf-charcoal;\n\n    &__top {\n        background-color: $pf-off-white;\n\n        &__wrapper {\n            display: flex;\n            flex-direction: column;\n            gap: $spacing--md;\n\n            @include media-breakpoint(lg) {\n                flex-direction: row;\n            }\n        }\n\n        &__col1 {\n            display: flex;\n            flex-direction: column;\n            gap: $spacing--md;\n            padding: $spacing--md 0;\n\n            @include media-breakpoint(lg) {\n                flex-basis: 50%;\n            }\n\n        }\n\n        &__logo {\n            p {\n                display: inline !important;\n            }\n        }\n\n        &__nav {\n            @include media-breakpoint(md) {\n                margin-left: 3.5rem;\n            }\n\n            ul {\n                display: flex;\n                flex-wrap: wrap;\n                gap: 1rem;\n\n                li {\n                    width: calc(50% - 1rem);\n\n                    a {\n                        &:hover {\n                            text-decoration: underline;\n                        }\n                    }\n                }\n            }\n        }\n\n        &__col2 {\n            @include media-breakpoint(lg) {\n                flex-basis: 50%;\n            }\n        }\n\n        &__icons {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            flex-wrap: wrap;\n            gap: 1rem;\n\n            &__img {\n                width: 250px;\n                height: 150px;\n                display: flex;\n                justify-content: center;\n                align-items: center;\n\n                img {\n                    max-width: 100%;\n                    max-height: 100%;\n                    object-fit: contain;\n                }\n            }\n\n            &.multiple-icons {\n                .footer__top__icons__img {\n                    width: 200px;\n                    height: 100px;\n                }\n\n                &:only-child .footer__top__icons__img {\n                    width: 250px;\n                    height: 160px;\n                }\n            }\n\n            @include media-breakpoint(lg) {\n                &.multiple-icons {\n                    .footer__top__icons__img {\n                        width: 200px;\n                        height: 100px;\n                    }\n\n                    &:only-child .footer__top__icons__img {\n                        width: 250px;\n                        height: 160px;\n                    }\n                }\n            }\n        }\n\n        &__form {\n            margin-bottom: $spacing--lg;\n            background-color: #D9D9D9;\n            padding: $spacing--sm;\n\n            @include media-breakpoint(lg) {\n                clip-path: polygon(0 0, 90% 0, 100% 100%, 10% 100%);\n                margin-top: -$spacing--xs;\n\n                padding: $spacing--xs $spacing--xl;\n            }\n        }\n\n        &__icons {\n            margin-bottom: $spacing--md;\n        }\n    }\n\n\n    &__bottom {\n        background-color: black;\n\n        &__content {\n            display: flex;\n            flex-direction: column;\n            justify-content: center;\n            align-items: center;\n            gap: $spacing--md;\n\n            padding-top: rem(44);\n            padding-bottom: rem(44);\n\n            @include media-breakpoint(lg) {\n                flex-direction: row;\n                justify-content: space-between;\n            }\n        }\n    }\n\n    &__social {\n        display: flex;\n        justify-content: center;\n        gap: 2rem;\n\n        i {\n            color: $pf-charcoal;\n        }\n    }\n\n    &__upper {\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        gap: 1rem;\n        padding-bottom: 3rem;\n\n        .button {\n            width: fit-content;\n            margin: 0 auto;\n\n            @include media-breakpoint(md) {\n                margin: 0;\n            }\n        }\n\n        @include media-breakpoint(md) {\n            flex-direction: row;\n            justify-content: space-between;\n        }\n    }\n\n    &__middle {\n        padding-bottom: 3rem;\n    }\n\n    &__columns-wrapper {\n        display: flex;\n        flex-direction: column;\n        gap: 1rem;\n\n        @include media-breakpoint(md) {\n            display: grid;\n            grid-template-columns: repeat(3, minmax(0, 1fr)) minmax(0, 2fr);\n        }\n    }\n\n    &__column {\n        display: flex;\n        flex-direction: column;\n        gap: 1rem;\n        padding-bottom: 1rem;\n\n        h3 {\n            font-weight: 500;\n            font-size: $size--md;\n            text-transform: none;\n        }\n\n        a {\n            font-weight: 300;\n            text-decoration: none;\n            color: $pf-charcoal;\n\n            &:hover {\n                text-decoration: underline;\n            }\n        }\n    }\n\n    &__newsletter {\n        h3 {\n            font-weight: 500;\n            font-size: $size--md;\n            text-transform: none;\n        }\n    }\n\n    &__lower {\n        background-color: $pf-charcoal;\n        color: $pf-white;\n        text-align: center;\n        padding: 2rem 0;\n\n        p,\n        a {\n            font-size: $size--sm;\n        }\n\n        a {\n            text-decoration: underline;\n        }\n    }\n}\n\n#backToTopBtn {\n    position: fixed;\n    visibility: hidden;\n    z-index: 899;\n    bottom: 4rem;\n    right: 1rem;\n    width: 80px;\n    height: 80px;\n    border-radius: 50%;\n    background-color: $pf-charcoal;\n    color: $pf-white;\n    filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.50));\n    text-transform: uppercase;\n    font-size: $size--sm;\n    padding: 0.5rem;\n    padding-top: 0;\n\n    span {\n        display: block;\n        font-size: $size--md;\n    }\n\n    @include media-breakpoint(md) {\n        width: 105px;\n        height: 105px;\n        right: 2rem;\n        font-size: $size--base;\n\n        span {\n            font-size: $size--lg;\n        }\n    }\n\n    &:hover {\n        transform: scale(1.05);\n    }\n\n    &.show {\n        visibility: visible;\n        animation: slideIn 0.3s ease-in-out;\n        animation-fill-mode: forwards;\n    }\n\n    &.hide {\n        animation: slideOut 0.3s ease-in-out;\n        animation-fill-mode: forwards;\n    }\n}\n\n/* Define slide-in animation */\n@keyframes slideIn {\n    from {\n        transform: translateX(150%);\n    }\n\n    to {\n        transform: translateX(0);\n    }\n}\n\n/* Define slide-out animation */\n@keyframes slideOut {\n    from {\n        transform: translateX(0);\n    }\n\n    to {\n        transform: translateX(150%);\n    }\n}",".container {\n  position: relative;\n  margin: 0 auto;\n  width: 100%;\n  padding: 0 $size--md;\n  box-sizing: border-box;\n\n  @include media-breakpoint('sm') {\n    max-width: $col--sm;\n  }\n\n  @include media-breakpoint('md') {\n    max-width: $col--md;\n  }\n\n  @include media-breakpoint('lg') {\n    max-width: $col--lg;\n  }\n\n  @include media-breakpoint('xl') {\n    max-width: $col--xl;\n  }\n}\n","@mixin media-breakpoint($breakpoint, $property: min-width) {\n  @media ($property: map-deep-get($breakpoints, $breakpoint, breakpoint)) { @content; }\n}\n\n",".full-bleed {\n    margin-left: calc((100vw - 100%) / -2);\n    width: 100vw;\n}\n",".flow > * + * {\n    margin-top: var(--flow-space, 1.5em);\n}",".visually-hidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: auto;\n    margin: 0;\n    left: -10000px;\n    opacity: 0;\n    transform: scale(0);\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n    white-space: nowrap;\n  }",".block-padding {\n    padding-top: $spacing--lg;\n    padding-bottom: $spacing--lg;\n\n    @include media-breakpoint('md') {\n        padding-top: $spacing--xl;\n        padding-bottom: $spacing--xl;\n    }\n}\n\n.block-padding--top {\n    padding-top: $spacing--lg;\n\n    @include media-breakpoint('md') {\n        padding-top: $spacing--xl;\n    }\n}\n\n.block-padding--bottom {\n    padding-bottom: $spacing--lg;\n\n    @include media-breakpoint('md') {\n        padding-bottom: $spacing--xl;\n    }\n}",".overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.3);\n}",".left {\n    text-align: left;\n}\n\n.right {\n    text-align: right;\n}\n\n.center {\n    text-align: center;\n}",".caret {\n  background-image: url('../../../images/icons/caret_white.svg');\n  height: 25px;\n  width: 25px;\n  background-repeat: no-repeat;\n  background-position: center;\n  transform: rotate(0);\n  transition: rotate 300ms ease-out;\n  display: inline-block;\n}\n\n.header {\n  position: fixed;\n  left: 0;\n  top: 0;\n  height: $header-height-mobile;\n  font-size: $size--base;\n  z-index: 10000;\n\n  transition: background-color 250ms ease-in-out,background-position 250ms ease-in-out,box-shadow 250ms ease-in-out,padding 250ms ease-in-out;\n  background-color: $pf-raspberry;\n\n  &__transparent-start {\n    background-color: rgba(153, 48, 80,0);\n    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.25), rgba(35, 47, 74, 0) 50%);\n    background-size: 100% 200%;\n    background-repeat: no-repeat;\n  }\n\n  &.js-menu-scrolled {\n    background-color: $pf-raspberry;\n    background-position-y: 100%;\n    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.30);\n  }\n\n  &--default {\n    .menu-item {\n      a {\n        color: $pf-white;\n      }\n    }\n  }\n\n  &--blog {\n    color: $pf-white;\n    @extend .js-menu-scrolled;\n  }\n\n  @include media-breakpoint('lg') {\n    height: $header-height-desktop;\n  }\n\n  &__wrapper {\n    height: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 0 1.125rem;\n\n    @include media-breakpoint('lg') {\n      padding: 0 2rem;\n    }\n\n    @include media-breakpoint('xl') {\n      padding: 0 3rem;\n    }\n\n    @include media-breakpoint('xxl') {\n      padding: 0 8rem;\n    }\n\n    &__nav {\n      height: 100%;\n      display: flex;\n      justify-content: flex-end;\n\n      &__inner {\n        height: 100%;\n        display: flex;\n        align-items: center;\n        justify-content: flex-end;\n        width: fit-content;\n      }\n    }\n  }\n\n  .nav {\n\n    /*\n  * MOBILE STYLING\n  */\n    @media screen and (max-width: 991px) {\n      background-color: $pf-raspberry;\n      color: $pf-off-white;\n      position: absolute;\n      left: 0;\n      top: $header-height-mobile - 1;\n      width: 100%;\n      max-height: 0;\n      transition: max-height 1s;\n      transition-delay: 0.2s;\n      overflow: hidden;\n      @include full-bleed;\n\n      &.drop-open {\n        height: fit-content;\n        max-height: 2000px;\n        transition: max-height 1s;\n        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.30);\n      }\n\n      .main-navigation {\n        margin-top: 2rem;\n      }\n\n      .menu-item {\n        & .sub-menu-btn {\n          display: block;\n\n          &:hover,\n          &:focus {\n            background: $pf-secondary-raspberry;\n          }\n        }\n\n        & .sub-menu {\n          display: unset;\n        }\n\n        a {\n          padding: 1em 2em;\n          display: inline-block;\n          width: 100%;\n          color: inherit;\n\n          &:hover,\n          &:focus,\n          &[aria-expanded='true'] {\n            background: $pf-secondary-raspberry;\n          }\n        }\n      }\n\n      .menu-item-has-children {\n        display: flex;\n        flex-wrap: wrap;\n        align-items: center;\n\n        &>a {\n          flex: 1 0 80%;\n        }\n\n        .sub-menu-btn {\n          flex: 1 0 10%;\n          height: 64px;\n        }\n\n        .sub-menu {\n          overflow: hidden;\n          max-height: 0;\n          transition: max-height 1s;\n          visibility: hidden;\n          flex-basis: 100%;\n\n          a {\n\n            &:hover,\n            &:focus {\n              background: $pf-raspberry;\n              outline-offset: -0.25rem;\n            }\n          }\n        }\n\n        &.open {\n          background: $pf-raspberry;\n\n          .sub-menu {\n            height: fit-content;\n            max-height: 500px;\n            visibility: visible;\n\n            a {\n              padding-left: 4rem;\n            }\n          }\n        }\n      }\n    }\n\n    /*\n  * DESKTOP STYLING\n  */\n    @include media-breakpoint('lg') {\n      display: none;\n      height: 100%;\n      font-weight: 500;\n      text-transform: uppercase;\n\n      ul.main-navigation {\n        height: 100%;\n        display: flex;\n        align-items: center;\n\n        &>.menu-item {\n          height: 100%;\n          display: flex;\n          align-items: center;\n          padding: 0 1rem;\n          position: relative;\n\n          &:hover,\n          &:focus-within {\n            .caret {\n              background-image: url('../../../images/icons/caret_white.svg');\n            }\n          }\n        }\n\n        &>li>a {\n          font-weight: 500;\n          color: $pf-white;\n        }\n      }\n\n      @include media-breakpoint('lg') {\n        display: block;\n      }\n\n      // Has submenu\n      .menu-item-has-children {\n        width: fit-content;\n        \n        &::after {\n          position: absolute;\n          z-index: 921;\n          content: \"\";\n          width: 0;\n          height: 0;\n          border: 10px solid rgba(0,0,0,0);\n          border-bottom-color: $pf-white;\n          bottom: 0;\n          left: 50%;\n          margin-left: -10px;\n          visibility: hidden;\n          opacity: 0;\n          transition: opacity 300ms linear;\n        }\n\n        .sub-menu {\n          display: block;\n          visibility: hidden;\n          background-color: $pf-white;\n          position: absolute;\n          left: 0;\n          top: 100%;\n          max-height: 0;\n          width: 100vw;\n          margin-left: calc((100vw - 100%) / -2);\n          padding: 2rem 10rem;\n          opacity: 0;\n          transition: opacity 300ms linear;\n\n          // Add a ::before that is full window width and height to allow for a full bleed background\n          &::before {\n            content: '';\n            position: absolute